@import '~src/css/quasar.variables.scss', 'quasar/src/css/variables.sass';
.input {
  &-white {
    & .q-field__inner {
      background-color: white;
    }
  }
  &-split {
    &.q-field {
      &.format-bold {
        & .q-field {
          &__control {
            &-container {
              &:before {
                border-width: map-get($borders, 'md');
                border-color: $primary;
              }
            }
          }
        }
      }
      & .q-field {
        &__control {
          padding-right: 0;
          &:before,
          &:after {
            border-color: $border-color;
          }
          &-container {
            &:before {
              content: '';
              position: absolute;
              top: 0;
              right: 0;
              bottom: 0;
              left: 0;
              pointer-events: none;
              border: 1px solid $border-color;
              border-radius: 0 $border $border 0;
              z-index: 2;
            }
            padding: 0 12px 0 6px;
            border-radius: 0 $border $border 0;
            z-index: 1;
          }
        }
        &__prepend {
          padding-right: 0;
        }
      }
      &--focused,
      &--highlighted,
      &:active,
      &:focus-visible,
      &:focus-within,
      &:focus-within {
        &:not(.q-field--standard, .q-field--error) {
          & .q-field__control-container {
            box-shadow: map-get($gm-shadows, 'active');
          }
        }
      }
      &--focused,
      &--highlighted,
      &:hover,
      &:active,
      &:focus-visible,
      &:focus-within,
      &:focus-within {
        & .q-field {
          &__control {
            &-container {
              &:before {
                border-color: $secondary;
              }
            }
          }
        }
      }
      &--error {
        &.q-field {
          &--focused,
          &--highlighted,
          &:active,
          &:focus-visible,
          &:focus-within,
          &:focus-within {
            &:not(.q-field--standard) {
              & .q-field__control-container {
                box-shadow: map-get($gm-shadows, 'error');
                clip-path: inset(
                  -#{$shadow-state-size} 0px -#{$shadow-state-size} -#{$shadow-state-size}
                );
              }
            }
          }
          &--focused,
          &--highlighted,
          &:hover,
          &:active,
          &:focus-visible,
          &:focus-within,
          &:focus-within {
            & .q-field {
              &__control {
                &-container {
                  border-radius: 0;
                  &:before {
                    border-color: $negative;
                    border-right: 0;
                    border-radius: 0;
                  }
                }
              }
            }
          }
          .q-field__append {
            position: relative;
            padding: 0 10px;
            &:before {
              content: '';
              position: absolute;
              top: 0;
              right: 0;
              bottom: 0;
              left: 0;
              pointer-events: none;
              border: 1px solid $negative;
              border-radius: 0 $border $border 0;
              z-index: 3;
              box-shadow: map-get($gm-shadows, 'error');
              clip-path: inset(
                -#{$shadow-state-size} -#{$shadow-state-size} -#{$shadow-state-size}
                  0px
              );
              border-left: 0;
            }
          }
        }
      }
    }
  }
}
